.transparent-header .home-article-full {
    margin-top: 0;
}

.home-article-full {
    margin-top: 89px;
    margin-bottom: 62px;
    display: flex;
}

.home-article-full-img {
    margin-left: 40px;
    width: 760px;
}

.home-article-full-img a {
    display: block;
    height: 100%;
    overflow: hidden;
}

.home-article-full-img img {
    display: block;
    width: 100%;
    max-height: 470px;
    transition: transform .5s ease;
}

.home-article-full-img a:hover img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.home-article-full-content {
    display: flex;
    align-items: end;
    flex-direction: column;
    width: 360px;
    border-bottom: 1px solid #ccc;
}

.home-article-full .headline h2 {
    margin: 0 0 20px;
    font-size: 45px;
    font-weight: 900;
    line-height: 45px;
}

.home-article-full .headline h2 a:hover {
    text-decoration: underline;
}

.home-article-full .underline {
    margin-bottom: 72px;
    color: #393746;
    font-size: 16px;
    font-weight: normal;
    line-height: 21px;
    text-align: right;
}

.home-article-full .date {
    margin-bottom: 20px;
    color: #74737D;
    font-family: "Miriam Libre";
    font-size: 14px;
    font-weight: normal;
    line-height: 15px;
}